<h1 id="小程序" tabindex="-1"><a class="header-anchor" href="#小程序" aria-hidden="true">#</a> 小程序</h1>
<h2 id="登录获取用户信息" tabindex="-1"><a class="header-anchor" href="#登录获取用户信息" aria-hidden="true">#</a> 登录获取用户信息</h2>
<blockquote>
<p>注意：需要关联小程序，并且使用关联后的小程序AgentId与Secret。</p>
</blockquote>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$config</span> <span class="token operator">=</span> <span class="token punctuation">[</span>
    <span class="token string single-quoted-string">'corp_id'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'xxxxxxxxxxxxxxxxx'</span><span class="token punctuation">,</span> <span class="token comment">//企业id</span>

    <span class="token string single-quoted-string">'agent_id'</span> <span class="token operator">=></span> <span class="token number">100020</span><span class="token punctuation">,</span> <span class="token comment">// 企业微信关联后的AgentId</span>
    <span class="token string single-quoted-string">'secret'</span>   <span class="token operator">=></span> <span class="token string single-quoted-string">'xxxxxxxxxx'</span><span class="token punctuation">,</span> <span class="token comment">//企业微信关联后的Secret</span>
<span class="token punctuation">]</span><span class="token punctuation">;</span>

<span class="token variable">$app</span> <span class="token operator">=</span> <span class="token class-name static-context">Factory</span><span class="token operator">::</span><span class="token function">work</span><span class="token punctuation">(</span><span class="token variable">$config</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

<span class="token variable">$miniProgram</span> <span class="token operator">=</span> <span class="token variable">$app</span><span class="token operator">-></span><span class="token function">miniProgram</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

<span class="token variable">$res</span> <span class="token operator">=</span> <span class="token variable">$miniProgram</span><span class="token operator">-></span><span class="token property">auth</span><span class="token operator">-></span><span class="token function">session</span><span class="token punctuation">(</span><span class="token string double-quoted-string">"js-code"</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br></div></div>